.menu-tab {
  display: flex;
  justify-content: center;
}
.menu-tab button {
  padding: 10px 20px;
  background-color: #ffb400;
  color: #fff;
  border: none;
  border-right: 1px solid #888;
  font-weight: bold;
  letter-spacing: 1px;
}
.menu-tab button:nth-child(1) {
  padding: 10px 25px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.menu-tab button:nth-child(6) {
  padding: 10px 25px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}
.project_img {
  box-shadow: 5px 8px 6px #777777;
  max-height: 300px;
  height: 200px;
  width: 100%;
  transition: -webkit-transform 0.8s;
  transition: transform 0.8s;
  transition: transform 0.8s, -webkit-transform 0.8s;
  border-radius: 4px;
  margin-bottom: 1rem;
}
.project_img:hover {
  box-shadow: 5px 8px 6px #ffb400;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: #fff;
  opacity: 1;
}

@media (max-width: 768px) {
  .menu-tab button {
    padding: 6px 6px;
    font-size: 10px;
  }
  .menu-tab button:nth-child(1),
  .menu-tab button:nth-child(6) {
    padding: 10px 20px;
  }
  .project_img {
    height: auto;
  }
}

